.body{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:0% 10%;
    width:100%;
    height: 100%;
    min-height: 100vh;
    color:#ffffff;
}

.me-container{
    display:flex;
    flex-direction: row;
    justify-content:center;
}

.intro{
    font-size: 7rem;
    margin-right: 1rem;
}

.text-color-red{
    color:#ea0f1e;
} 

.side-image-container img{
    width:50%;
}


@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .body {
        flex-direction: row-reverse;
        align-items: center;
    }
    
    .me-container{
        flex-direction:column;
        align-items:flex-start;
    }
    .intro{
        font-size: 4rem;
    }

    .side-image-container img{
        width: 130%;
    }
    

  }